.main h3 {
  font-size: 24px;
  line-height: 30px;
  font-weight: bold;
  background-image: linear-gradient(0deg, #b7fff5, #fffea7);
  color: transparent;
  background-size: 100%;
  background-clip: text;
}

.body {
  position: relative;
  padding: 2vmin;
  border-radius: 5px;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, #b7fff500, #ffffff1f);
  pointer-events: none;
}

div.list_container {
  height: 50vmin;
  overflow: auto;
  font-family: Merriweather, DFKai-sb;
}

div.word_content {
  color: #10a37f;
  background-color: #ebfccc;
}
